}

.gallery-background {
  background-color: #57423F;
  position: relative;
  padding-bottom: 50px;
}

.gallery-shadow {
  /* filter: drop-shadow(-1px 5px 10px rgba(0,0,0,0.35)); */
  filter: drop-shadow(0px 5px 27px rgba(0,0,0,0.75));
}

.gallery-container {
  height: 95vh;
  clip-path: polygon(0 20vh, 100% 0, 100% 80vh, 0 100%);
  background-color: white;
}


.parent {
  width: 25%; 
  height: 100%;
  display: inline-block;
  cursor: pointer;
  transition: all .5s;
}

.child {
  width: 100%;
  height: 100%;
  background-color: black; /* fallback color */  
  background-position: center;
  background-size: cover;
}

.parent:nth-child(1) {  
  position: absolute;
  top: 0;
  left: 0;
}

.parent:nth-child(2) {  
  position: absolute;
  top: 0;
  left: 25%;
}

.parent:nth-child(3) {  
  position: absolute;
  top: 0;
  right: 25%;
}

.parent:nth-child(4) {  
  position: absolute;
  top: 0;
  right: 0;
}

.parent:hover ,
.parent:focus {
  z-index: 100;
  width: 33%;
}

