    box-shadow: 10px 15px 21px -5px rgba(0,0,0,0.75);
  }

  .footer-container {
    width: 100%;
    height: 70vh;
    background-color: #0f0b0b;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px 20px 10px;
  }

  .footer-container h1 {
    font-size: 1.5rem;
  }

  .footer-container h2 {
    font-size: 1.2rem;
  }
}